import React from 'react'
import firebase from '../Firebase';

class KeysNew extends React.Component {
    constructor(props){
        super(props)
        this.state = {
            keys: [],
            form: {}
        };
    }

    handleChange = e => {
        this.setState({
            form: {
                ...this.state.form,
                [e.target.name]: e.target.value
            }
        })
    }

    schoolName = () => this.props.schools.filter(x => x.id === this.state.form.schoolId)[0].name

    create_UUID(){
        var dt = new Date().getTime();
        var uuid = 'xxxxxyxx'.replace(/[xy]/g, function(c) {
            var r = (dt + Math.random()*16)%16 | 0;
            dt = Math.floor(dt/16);
            return (c === 'x' ? r :(r &0x3|0x8)).toString(16);
        });
        return uuid;
    }
    onSubmit = e => {
        e.preventDefault();
        
        for (let index = 0; index < this.state.form.keys; index++) {
            const key = this.create_UUID()
            const self = this
            firebase.database().ref('keys').child(`${key}`).set({
                grade: this.state.form.grade,
                group: this.state.form.group,
                school: this.schoolName(),
                schoolId: this.state.form.schoolId,
                status: 'pending'
            }, function(error) {
                if (error) {
                    console.log(error)
                } else {
                    self.addKey(key)
                }
          })
        }
    }

    addKey(key){
        var newArray = this.state.keys.slice()
        newArray.push(key);   
        this.setState({keys: newArray})
    }

    table(){
        return (
            <table className="table SchoolNew-top-space">
                <thead>
                    <tr>
                        <th scope="col">Key</th>
                    </tr>
                </thead>
                <tbody>
                    { this.state.keys.map((key) => {
                        return (
                            <tr>
                                <td>{key}</td>
                            </tr>
                        )
                    })}
                </tbody>
            </table>
        )
    }
    render(){
        return(
            <div>
                <div className="row">
                    <div className="col-sm">
                        <form onSubmit={this.onSubmit}>
                        <div className="SchoolNew-top-space">
                            <input 
                                type="text" 
                                className="form-control" 
                                value={this.state.form.grade} 
                                onChange={this.handleChange} 
                                name="grade"
                                placeholder="Grade"
                            />
                        </div>
                        <div className="SchoolNew-top-space">
                            <input 
                                type="text" 
                                className="form-control" 
                                value={this.state.form.group} 
                                onChange={this.handleChange} 
                                name="group"
                                placeholder="Group"
                            />
                        </div>
                        <div className="form-group SchoolNew-top-space">
                            <select className="form-control" onChange={this.handleChange} value={this.state.form.schoolId} name="schoolId">
                                <option>Selecciona una opci√≥n</option>
                                { this.props.schools.map((school) => {
                                    return <option key={school.id} value={school.id}>{school.name}</option>
                                })}
                            </select>
                        </div>
                        <div className="SchoolNew-top-space">
                            <input 
                                type="number" 
                                className="form-control" 
                                value={this.state.form.keys} 
                                onChange={this.handleChange} 
                                name="keys"
                                placeholder="Keys"
                            />
                        </div>
                    <button className="btn btn-outline-primary SchoolNew-top-space float-right" type="submit">Generar</button>
                </form>
                    </div>
                    <div className="col-sm">
                        { this.table() }
                    </div>
                </div>
            </div>
           
        )
    }
}

export default KeysNew