import React from 'react'
import './styles/SchoolNew.css'
import firebase from '../Firebase';

class SchoolNew extends React.Component {
    constructor(props){
        super(props)
        this.state = {
            form: {}
        };
    }

    handleChange = e => {
        this.setState({
            form: {
                [e.target.name]: e.target.value
            }
        })
    }
    
    onSubmit = e => {
        e.preventDefault();
        const newSchoolId = firebase.database().ref().child('schools').push().key
        firebase.database().ref(`schools/${newSchoolId}`).update({
            name: this.state.form.name
          }, function(error) {
            if (error) {
                console.log(error)
              // The write failed...
            } else {
                console.log('successfull')
              // Data saved successfully!
            }
          })
    }

    render(){
        return(
            <form onSubmit={this.onSubmit}>
                <div className="input-group mb-3 SchoolNew-top-space">
                    <input 
                        type="text" 
                        className="form-control" 
                        placeholder="Colegio"
                        value={this.state.schoolName} 
                        onChange={this.handleChange} 
                        name="name"
                        placeholder="School Name"
                    />
                    <div className="input-group-append">
                        <button className="btn btn-outline-primary" type="submit">Agregar</button>
                    </div>
                </div>
            </form>
        )
    }
}

export default SchoolNew