import React from 'react'
import firebase from '../Firebase';
import KeyList from '../components/KeyList'

class Codes extends React.Component {
    constructor(props){
        super(props)
        this.keysRef = firebase.database().ref('keys')
        this.state = {
            codes: []
        }
    }

    componentDidMount(){
        this.keysRef.on('value', (snapshot) => {
            let newState = []
            let keys = snapshot.val()
            for(let key in keys){
                newState.push({
                    id: key,
                    grade: keys[key].grade,
                    group: keys[key].group,
                    school: keys[key].school,
                    status: keys[key].status
                })
            }
            this.setState({
                codes: newState
            })
        }) 
    }

    render(){
        return <KeyList keys={this.state.codes}/>
    }
}

export default Codes