import React from 'react'

const KeyList = (props) => (
    <table className="table SchoolNew-top-space">
    <thead>
        <tr>
            <th scope="col">#</th>
            <th>Grade</th>
            <th>Group</th>
            <th>School</th>
            <th>Key</th>
            <th>Status</th>
        </tr>
    </thead>
    <tbody>
        { props.keys.map((key, index) => {
            return (
                <tr key={key.id}>
                    <td>{index}</td>
                    <td>{key.grade}</td>
                    <td>{key.group}</td>
                    <td>{key.school}</td>
                    <td>{key.id}</td>
                    <td>
                        <span className={`badge ${key.status === 'pending' ? 'badge-primary' : 'badge-success'}`}>
                            {key.status}
                        </span>
                    </td>
                </tr>
            )
        })}
    </tbody>
</table>
)

export default KeyList