import React from 'react'
import KeysNew from '../components/KeysNew'
import firebase from '../Firebase'

class Keys extends React.Component {
    constructor(props){
        super(props)
        this.schoolsRef = firebase.database().ref('schools')
        this.state = {
            keys: [],
            schools: []
        }
    }

    componentDidMount() {
        this.schoolsRef.on('value', (snapshot) => {
            let newState = []
            let schools = snapshot.val()
            for(let school in schools){
                newState.push({
                    id: school,
                    name: schools[school].name
                })
            }
            this.setState({
                schools: newState
            })
        }) 
    }

    render(){
        return(
            <KeysNew schools={this.state.schools}/>
        )
    }
}

export default Keys